;\n    --popoverShadow: none;\n}\n}\n\n/* stylelint-disable-next-line selector-max-type */\nhtml {\n  padding: var(--spacingLarge);\n  font-family:\n    \"Helvetica Neue\",\n    Arial,\n    \"Hiragino Kaku Gothic ProN\",\n    \"Hiragino Sans\",\n    Meiryo,\n    sans-serif;\n  font-size: var(--fontSizeMedium);\n  line-height: 1;\n  color: var(--fg);\n  background-color: var(--bg);\n}\n", "",{"version":3,"sources":["webpack://./src/components/App.vue"],"names":[],"mappings":";AAuIA;EACE,WAAW;EACX,UAAU;EACV,UAAU;EACV,mBAAmB;EACnB,uBAAuB;EACvB,kBAAkB;EAClB,iBAAiB;EACjB,sBAAsB;EACtB,cAAc;EACd,eAAe,EAAE,uBAAuB;EACxC,uBAAuB,EAAE,WAAW;EACpC,wBAAwB,EAAE,WAAW;EACrC,iDAAiD;EACjD,iBAAiB,EAAE,mBAAmB;EACtC,sBAAsB,EAAE,WAAW;EACnC,uBAAuB,EAAE,WAAW;EACpC,gDAAgD;;EAEhD,eAAe;EACf,qBAAqB;EACrB,sBAAsB;EACtB,qBAAqB;EACrB,sBAAsB;EACtB,yBAAyB;EACzB,oBAAoB;EACpB,oBAAoB;EACpB,8BAA8B;;EAE9B,WAAW;EACX,uBAAuB;EACvB,8BAA8B;EAC9B,uBAAuB;EACvB,oBAAoB;EACpB,wBAAwB;EACxB,mBAAmB;EACnB,gBAAgB;EAChB,0CAA0C;EAC1C,qBAAqB;EACrB,wBAAwB;EACxB,mBAAmB;;EAEnB,UAAU;EACV,yBAAyB;EACzB,6FAA6F;EAC7F,6FAA6F;EAC7F,2BAA2B;EAC3B,0BAA0B;EAC1B,wBAAwB;EACxB,4BAA4B;EAC5B,yBAAyB;EACzB,gCAAgC;EAChC,qBAAqB;EACrB,yEAAyE;EACzE,8EAA8E;AAChF;AAEA;AACE;IACE,UAAU;IACV,UAAU;IACV,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;IACjB,cAAc;IACd,qBAAqB;AACvB;AACF;;AAEA,kDAAkD;AAClD;EACE,4BAA4B;EAC5B;;;;;;cAMY;EACZ,gCAAgC;EAChC,cAAc;EACd,gBAAgB;EAChB,2BAA2B;AAC7B","sourcesContent":["<script lang=\"ts\">\nimport { defineComponent } from \"vue\";\nimport Analytics from \"../utils/analytics\";\nimport Header from \"./global/Header.vue\";\nimport Footer from \"./global/Footer.vue\";\nimport TextSource from \"./cards/TextSource.vue\";\nimport FileSource from \"./cards/FileSource.vue\";\nimport FukumojiSource from \"./cards/FukumojiSource.vue\";\nimport Target from \"./cards/Target.vue\";\nimport Result from \"./cards/Result.vue\";\nimport Tutorial from \"./cards/Tutorial.vue\";\nimport TabButton from \"./inputs/TabButton.vue\";\nimport TabGroup from \"./inputs/TabGroup.vue\";\nimport Space from \"./global/Space.vue\";\nimport Grid from \"./global/Grid.vue\";\nimport GridItem from \"./global/GridItem.vue\";\nimport Image from \"./icons/Image.vue\";\nimport Text from \"./icons/Text.vue\";\nimport Emoji from \"./icons/Emoji.vue\";\nimport \"../css/destyle.css\";\n\nexport default defineComponent({\n  components: {\n    TextSource,\n    FileSource,\n    FukumojiSource,\n    Target,\n    Result,\n    Tutorial,\n    TabButton,\n    TabGroup,\n    Header,\n    Footer,\n    Space,\n    Grid,\n    GridItem,\n    Image,\n    Text,\n    Emoji,\n  },\n  data() {\n    return {\n      baseImage: null as (HTMLImageElement | null),\n      resultImages: [[]] as Blob[][],\n      previewMode: false,\n      /* ui */\n      ui: {\n        mode: \"text\",\n        showTargetPanel: false,\n        showTargetDetails: false,\n      },\n    };\n  },\n  mounted() {\n    Analytics.switchMode(\"text\");\n  },\n  methods: {\n    onToggleShowTarget(): void {\n      this.ui.showTargetPanel = !this.ui.showTargetPanel;\n      Analytics.switchMode(this.ui.showTargetPanel ? \"target\" : this.ui.mode, true);\n    },\n    onSelectMode(value: string): void {\n      this.ui.mode = value;\n      this.ui.showTargetPanel = false;\n      Analytics.switchMode(value, true);\n    },\n    onRenderTarget(imgs: Blob[][]): void {\n      this.resultImages = imgs;\n      Analytics.render();\n    },\n    onRender(img: HTMLImageElement): void {\n      this.baseImage = img;\n    },\n  },\n});\n</script>\n\n<template>\n  <div class=\"app\">\n    <Space vertical large full>\n      <Header />\n\n      <TabGroup>\n        <TabButton :model-value=\"ui.mode\" value=\"text\" @update:model-value=\"onSelectMode\">\n          <template #icon>\n            <Text />\n          </template>\n          テキスト\n        </TabButton>\n        <TabButton :model-value=\"ui.mode\" value=\"file\" @update:model-value=\"onSelectMode\">\n          <template #icon>\n            <Image />\n          </template>\n          画像ファイル\n        </TabButton>\n        <TabButton :model-value=\"ui.mode\" value=\"parts\" @update:model-value=\"onSelectMode\">\n          <template #icon>\n            <Emoji />\n          </template>\n          パーツ\n        </TabButton>\n      </TabGroup>\n\n      <Grid :columns=\"[[760, 1], [Infinity, 3]]\" spaced>\n        <GridItem :span=\"2\">\n          <TextSource\n              :show=\"ui.mode == 'text' && !ui.showTargetPanel\"\n              @render=\"onRender\" />\n          <FileSource\n              :show=\"ui.mode == 'file' && !ui.showTargetPanel\"\n              @render=\"onRender\" />\n          <FukumojiSource\n              :show=\"ui.mode == 'parts' && !ui.showTargetPanel\"\n              @render=\"onRender\" />\n          <Target\n              :show=\"ui.showTargetPanel\"\n              :base-image=\"baseImage\"\n              @render=\"onRenderTarget\" />\n        </GridItem>\n        <GridItem>\n          <Tutorial v-if=\"!baseImage\" />\n          <Result\n              v-else\n              :images=\"resultImages\"\n              :show-target=\"ui.showTargetPanel\"\n              @toggle-show-target=\"onToggleShowTarget\" />\n        </GridItem>\n      </Grid>\n\n      <Footer />\n    </Space>\n  </div>\n</template>\n\n<style>\n:root {\n  /* colors */\n  --fg: #333;\n  --bg: #fff;\n  --accentBg: #fafafa;\n  --elevatedBg: var(--bg);\n  --light: var(--bg);\n  --dark: var(--fg);\n  --distantFg: var(--bg);\n  --border: #aaa;\n  --primary: #ea0; /* okhsl(80, 100, 75) */\n  --primaryHover: #ffb81c; /* l = 80 */\n  --primaryActive: #de9e00; /* l = 70 */\n  --primaryShadow: 0 0 0 2px rgba(238, 170, 0, 0.2);\n  --danger: #ff6261; /* h = 24, l = 65 */\n  --dangerHover: #ff7f7a; /* l = 70 */\n  --dangerActive: #ff3a45; /* l = 60 */\n  --dangerShadow: 0 0 0 2px rgba(255, 98, 97, 0.2);\n\n  /* typography */\n  --fontSizeTitle: 28px;\n  --fontSizeXLarge: 18px;\n  --fontSizeLarge: 16px;\n  --fontSizeMedium: 14px;\n  --fontSizeSmallIcon: 16px;\n  --fontSizeIcon: 26px;\n  --fontSizePart: 48px;\n  --multilineTextLineHeight: 1.5;\n\n  /* layout */\n  --spacingSmall: 0.25rem;\n  --spacingInlineSmall: 0.375rem;\n  --spacingMedium: 0.5rem;\n  --spacingLarge: 1rem;\n  --spacingXLarge: 1.75rem;\n  --paddingV: 0.75rem;\n  --paddingH: 1rem;\n  --padding: var(--paddingV) var(--paddingH);\n  --paddingMinimal: 4px;\n  --borderRadiusSmall: 1px;\n  --borderRadius: 2px;\n\n  /* other */\n  --textareaLineHeight: 1.4;\n  --selectPadding: var(--paddingV) calc(var(--paddingH) + 1rem) var(--paddingV) var(--paddingH);\n  --numberPadding: var(--paddingV) calc(var(--paddingH) + 2rem) var(--paddingV) var(--paddingH);\n  --sliderRailHeight: 0.375em;\n  --sliderMarkHeight: 0.75em;\n  --sliderKnobSize: 1.25em;\n  --sliderValueMargin: 0.125em;\n  --sliderValueWidth: 2.5em;\n  --colorSliderRailHeight: 1.125em;\n  --mediaIconSize: 34px;\n  --popoverShadow: rgb(0 0 0 / 19%) 0 10px 20px, rgb(0 0 0 / 23%) 0 6px 6px;\n  --tabButtonPadding: 0 calc(var(--paddingH) * 0.75) calc(var(--paddingV) - 3px);\n}\n\n@media (prefers-color-scheme: dark) {\n  :root {\n    --fg: #eee;\n    --bg: #222;\n    --accentBg: #333;\n    --elevatedBg: #555;\n    --light: var(--fg);\n    --dark: var(--bg);\n    --border: #777;\n    --popoverShadow: none;\n  }\n}\n\n/* stylelint-disable-next-line selector-max-type */\nhtml {\n  padding: var(--spacingLarge);\n  font-family:\n    \"Helvetica Neue\",\n    Arial,\n    \"Hiragino Kaku Gothic ProN\",\n    \"Hiragino Sans\",\n    Meiryo,\n    sans-serif;\n  font-size: var(--fontSizeMedium);\n  line-height: 1;\n  color: var(--fg);\n  background-color: var(--bg);\n}\n</style>\n"],"sourceRoot":""}]);
