n  },\n});\n</script>\n\n<template>\n  <input ref=\"input\" type=\"file\" style=\"display: none;\" :accept=\"accept\" @change=\"onChange\">\n  <Button type=\"dashed\" @click=\"onClick\">\n    <slot />\n  </Button>\n  <div v-if=\"file\" class=\"file\">\n    <File /> {{ file.name }}\n  </div>\n</template>\n\n<style scoped>\n.file {\n  margin-top: var(--spacingMedium);\n  font-size: var(--fontSizeMedium);\n  color: var(--fg);\n  user-select: none;\n}\n</style>\n"],"sourceRoot":""}]);
