___CSS_LOADER_EXPORT___.push([module.id, "\n.tone-picker[data-v-95f25458] {\n  position: relative;\n  display: block;\n  width: 100%;\n  height: 320px; /* override me */\n}\n.container[data-v-95f25458] {\n  position: relative;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  cursor: pointer;\n  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */\n  touch-action: none;\n}\n.layer[data-v-95f25458] {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n}\n.knob[data-v-95f25458] {\n  position: absolute;\n  top: calc(100% * var(--jsValueV));\n  left: calc(100% * var(--jsValueH));\n  display: inline-block;\n  width: var(--sliderKnobSize);\n  height: var(--sliderKnobSize);\n  margin-top: calc(-1 * var(--sliderKnobSize) / 2);\n  margin-left: calc(-1 * var(--sliderKnobSize) / 2);\n  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */\n  cursor: grab;\n  background-color: transparent;\n  border-radius: calc(var(--sliderKnobSize) / 2);\n  box-shadow:\n    0 0 0 1px inset var(--border),\n    0 0 0 2px inset #fff;\n}\n.knob[data-v-95f25458]:hover {\n  box-shadow:\n    0 0 0 1px inset var(--primary),\n    0 0 0 2px inset #fff;\n}\n.knob.active[data-v-95f25458] {\n  box-shadow:\n    var(--primaryShadow),\n    0 0 0 1px inset var(--primaryActive),\n    0 0 0 2px inset #fff;\n}\n", "",{"version":3,"sources":["webpack://./src/components/inputs/TonePicker.vue"],"names":[],"mappings":";AAqEA;EACE,kBAAkB;EAClB,cAAc;EACd,WAAW;EACX,aAAa,EAAE,gBAAgB;AACjC;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;EACZ,gBAAgB;EAChB,eAAe;EACf,uEAAuE;EACvE,kBAAkB;AACpB;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,QAAQ;EACR,SAAS;EACT,OAAO;AACT;AAEA;EACE,kBAAkB;EAClB,iCAAiC;EACjC,kCAAkC;EAClC,qBAAqB;EACrB,4BAA4B;EAC5B,6BAA6B;EAC7B,gDAAgD;EAChD,iDAAiD;EACjD,uEAAuE;EACvE,YAAY;EACZ,6BAA6B;EAC7B,8CAA8C;EAC9C;;wBAEsB;AACxB;AAEA;EACE;;wBAEsB;AACxB;AAEA;EACE;;;wBAGsB;AACxB","sourcesContent":["<script lang=\"ts\">\nimport { defineComponent } from \"vue\";\nimport { HSV2HEX } from \"../../utils/color\";\n\nexport default defineComponent({\n  props: {\n    h: { type: Number, required: true },\n    s: { type: Number, required: true },\n    v: { type: Number, required: true },\n  },\n  emits: [\n    \"update:s\", \"update:v\",\n  ],\n  data: () => ({\n    moveHandler: null as ((e: PointerEvent) => void) | null,\n    upHandler: null as (() => void) | null,\n  }),\n  computed: {\n    baseColor(): string {\n      return HSV2HEX({ h: this.h, s: 100, v: 100 });\n    },\n  },\n  beforeUnmount() {\n    if (this.upHandler) {\n      this.upHandler();\n    }\n  },\n  methods: {\n    startDrag(evt: PointerEvent): void {\n      if (this.moveHandler) return;\n      const rect = (this.$refs.container as HTMLDivElement).getBoundingClientRect();\n      this.moveHandler = (e: PointerEvent) => {\n        const white = (e.clientX - rect.left) / rect.width;\n        const black = 1 - (e.clientY - rect.top) / rect.height;\n        this.$emit(\"update:s\", 100 * Math.min(1, Math.max(0, white)));\n        this.$emit(\"update:v\", 100 * Math.min(1, Math.max(0, black)));\n      };\n      this.upHandler = () => {\n        if (this.moveHandler) {\n          document.removeEventListener(\"pointermove\", this.moveHandler);\n          this.moveHandler = null;\n        }\n        if (this.upHandler) {\n          document.removeEventListener(\"pointerup\", this.upHandler);\n          document.removeEventListener(\"pointerleave\", this.upHandler);\n          this.upHandler = null;\n        }\n      };\n      document.addEventListener(\"pointermove\", this.moveHandler);\n      document.addEventListener(\"pointerup\", this.upHandler);\n      document.addEventListener(\"pointerleave\", this.upHandler);\n      this.moveHandler(evt);\n    },\n  },\n});\n</script>\n\n<template>\n  <div class=\"tone-picker\" :style=\"{ '--jsValueH': s / 100, '--jsValueV': 1 - v / 100 }\">\n    <div ref=\"container\" class=\"container\" @pointerdown=\"startDrag($event)\">\n      <div class=\"layer\" :style=\"{ background: baseColor }\" />\n      <div class=\"layer\" :style=\"{ background: `linear-gradient(90deg, white, transparent)` }\" />\n      <div class=\"layer\" :style=\"{ background: `linear-gradient(0deg, black, transparent)` }\" />\n      <div :class=\"['knob', { active: !!moveHandler }]\" />\n    </div>\n  </div>\n</template>\n\n<style scoped>\n.tone-picker {\n  position: relative;\n  display: block;\n  width: 100%;\n  height: 320px; /* override me */\n}\n\n.container {\n  position: relative;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  cursor: pointer;\n  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */\n  touch-action: none;\n}\n\n.layer {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n}\n\n.knob {\n  position: absolute;\n  top: calc(100% * var(--jsValueV));\n  left: calc(100% * var(--jsValueH));\n  display: inline-block;\n  width: var(--sliderKnobSize);\n  height: var(--sliderKnobSize);\n  margin-top: calc(-1 * var(--sliderKnobSize) / 2);\n  margin-left: calc(-1 * var(--sliderKnobSize) / 2);\n  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */\n  cursor: grab;\n  background-color: transparent;\n  border-radius: calc(var(--sliderKnobSize) / 2);\n  box-shadow:\n    0 0 0 1px inset var(--border),\n    0 0 0 2px inset #fff;\n}\n\n.knob:hover {\n  box-shadow:\n    0 0 0 1px inset var(--primary),\n    0 0 0 2px inset #fff;\n}\n\n.knob.active {\n  box-shadow:\n    var(--primaryShadow),\n    0 0 0 1px inset var(--primaryActive),\n    0 0 0 2px inset #fff;\n}\n</style>\n"],"sourceRoot":""}]);
